/** @format */

'use client';

import { useAuth } from '@/app/hooks/useAuth';
import { useProfile } from '@/app/hooks/useProfile';
import { addAlert } from '@/app/store/slices/alertSlice';
import { add } from 'date-fns';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';

const Dashboard = () => {
	const router = useRouter()
	const {addAlert} = useAlert();
	const { isAuthenticated } = useAuth();
	const { profile, getCurrentProfile, profileLoading } = useProfile();
	useEffect(() => {
		if (isAuthenticated && !profile && !profileLoading) {
			if (!profile) {
				addAlert({
					id: `${Date.now()}`,
					type: 'error',
					message: 'Please create your profile',
					duration: 5000,
				});

			}else{
			getCurrentProfile();
			}			
		}
	}, [isAuthenticated, profile, profileLoading, getCurrentProfile, router]);



	return (
		<>
			Dashboard
			<h2>{profile?.user.name}</h2>
		</>
	);
};

export default Dashboard;
